const data = {
  futureSimple: require('./grammar/tenses/future-simple.json'),
  pastSimple: require('./grammar/tenses/past-simple.json'),
  presentSimple: require('./grammar/tenses/present-simple.json'),
}

Object.keys(data).forEach((tenseName) => {
  let tense = data[tenseName];

  /**
   * 
   * @param {Object} params
   * @param {int} params.person - 1, 2, 3
   * @param {string} params.number - singular, plural
   * @param {string} params.sentenceType - positive, negative, question
   */
  tense.getAuxiliaryForm = (params) => {
    return tense[params.sentenceType][params.number][params.person].auxiliary
  }

  /**
   * 
   * @param {Object} params
   * @param {int} params.person - 1, 2, 3
   * @param {string} params.number - singular, plural
   * @param {string} params.sentenceType - positive, negative, question
   * @param {Object} params.verb
   */
  tense.getVerbForm = (params) => {
    return tense[params.sentenceType][params.number][params.person].verb
  }

})

module.exports = data

