const data = require('./grammar/personal-pronoun.json')

/**
 * 
 * @param {Object} params
 * @param {int} params.person - 1, 2, 3
 * @param {string} params.number - singular, plural
 * @param {string|null} params.gender - male, female, none
 */
data.getForm = (params) => {
  let forms = data[params.number][params.person]
  if (params.number == 'singular' && params.person == 3) {
    switch (params.gender) {
      case 'male':
        return forms[0]
      case 'female':
        return forms[1]
      default:
        return forms[2]
    }
  }

  return forms[0]
}

module.exports = data;

