const data = {
  work: require('./grammar/verbs/work.json')
}

/**
 * 
 * @param {Object} params
 * @param {string} params.form - V1, V2, ...
 * @param {string} params.verb - verb in default form
 */
data.getForm = (params) => {
  return data[params.verb].forms[params.form];
}

module.exports = data;

