const generator = require('./rules/sentence');

const verb = 'work'
const PERSONS = [1, 2, 3]
const NUMBERS = ['singular', 'plural']
const GENDERS = ['male', 'female', null]
const SENTENCE_TYPES = ['positive', 'negative', 'question']

const TENSES = [
  'Present Simple',
  'Past Simple',
  'Future Simple',
]

module.exports = TENSES.map((tense) => {
  let tenseGroup = {
    enabled: true,
    title: tense,
    sentences: {},
  };

  SENTENCE_TYPES.map((type) => {
    tenseGroup.sentences[type] = []

    NUMBERS.map((number) => {
      PERSONS.map((person) => {
        GENDERS.map((gender) => {
          let sentence = generator.generate({ tense, type, number, person, gender, verb })
          tenseGroup.sentences[type].push(sentence)
        })
      })
    })
    tenseGroup.sentences[type] = [...new Set(tenseGroup.sentences[type])]
  })

  return tenseGroup;
})
