const camelCase = require('lodash.camelcase')

const data = {
  verbs: require('./verb'),
  tenses: require('./tense'),
  personalPronouns: require('./personal-pronoun'),
}

/**
 * 
 * @param {Object} params
 * @param {string} params.type - positive, negative, question
 * @param {int}    params.person - 1, 2, 3
 * @param {string} params.number - singular, plural
 * @param {string} params.tense - present simple, past simple, ...
 * @param {string} params.verb
 * @param {string} params.gender - male, female, none
 */
data.generate = (params) => {
  let tense = data.tenses[camelCase(params.tense)]

  let auxiliary = tense.getAuxiliaryForm({
    sentenceType: params.type,
    person: params.person,
    number: params.number,
  })

  let verb = data.verbs.getForm({
    verb: params.verb,
    form: tense.getVerbForm({
      verb: params.verb,
      number: params.number,
      person: params.person,
      sentenceType: params.type,
    })
  })

  let personalPronoun = data.personalPronouns.getForm({
    person: params.person,
    number: params.number,
    gender: params.gender,
  })

  let words = []
  switch (params.type) {
    case 'positive':
      words = [personalPronoun, auxiliary, verb]
      break;
    case 'negative':
      words = [personalPronoun, auxiliary, verb]
      break;
    case 'question':
      words = [auxiliary, personalPronoun, verb]
      break;
    default:
      throw new Error(`invalid sentence type: ${params.type}`)
  }

  let sentence = words.join(' ')

  if (params.type == 'question') {
    sentence += '?'
  } else {
    sentence += '.'
  }

  sentence = sentence.substr(0, 1).toUpperCase() + sentence.substr(1);
  return sentence
}

module.exports = data;

