<script>
	import tenses from "./sentences";

	$: tenses;

	let currentSentence;

	tenses.map((tense) => {
		tense.id = tense.title.split(" ").join("_").toLowerCase();
		tense.failed = false;
	});

	const settings = {
		sentenceTypes: {
			positive: true,
			negative: true,
			question: true,
		},
	};

	function getRandomSentense() {
		let enabledTenses = tenses.filter((tense) => tense.enabled);
		if (!enabledTenses.length) return null;
		let tense = enabledTenses[Math.floor(Math.random() * enabledTenses.length)];
		let enabledSentenseTypes = Object.keys(settings.sentenceTypes).filter(
			(type) => settings.sentenceTypes[type]
		);
		if (!enabledSentenseTypes.length) return null;
		let sentenceType =
			enabledSentenseTypes[
				Math.floor(Math.random() * enabledSentenseTypes.length)
			];
		return {
			sentence:
				tense.sentences[sentenceType][
					Math.floor(Math.random() * tense.sentences[sentenceType].length)
				],
			sentenceType,
			tense,
		};
	}

	function updateCurrentSentense() {
		currentSentence = getRandomSentense();
		tenses.map((tense) => {
			tense.failed = false;
		});
		tenses = tenses;
	}

	function onSelectTense(tense) {
		if (currentSentence && currentSentence.tense.id == tense.id) {
			updateCurrentSentense();
		} else {
			tense.failed = true;
			tenses = tenses;
		}
	}

	updateCurrentSentense();
</script>

<div class="container">
	<!-- Sentence -->
	<div class="row" style="margin-top: 30px;">
		<div class="col">
			<p class="text-center ">
				<strong>
					{currentSentence.sentence}
				</strong>
			</p>
		</div>
	</div>
	<hr />

	<!-- Buttons -->
	<div class="row row-cols-3">
		{#each tenses as tense, i (tense.id)}
			{#if tense.enabled}
				<div class="col">
					<div class="form-group">
						<button
							class="btn btn-sm btn-block {tenses[i].failed
								? 'btn-danger'
								: 'btn-outline-primary'}"
							on:click={() => onSelectTense(tense)}
						>
							{tense.title}
						</button>
					</div>
				</div>
			{/if}
		{/each}
	</div>

	<hr />

	<!-- Settings -->
	<div class="row">
		<div class="col">
			<h3 class="text-center">Settings</h3>
		</div>
	</div>

	<div class="row">
		<div class="col">
			<h6>Enabled tenses:</h6>
			{#each tenses as tense}
				<div class="form-check">
					<input
						class="form-check-input"
						type="checkbox"
						bind:checked={tense.enabled}
						id={tense.id}
					/>
					<label class="form-check-label" for={tense.id}>
						{tense.title}
					</label>
				</div>
			{/each}
		</div>
	</div>

	<div class="row" style="margin-top: 10px;">
		<div class="col">
			<h6>Enabled sentence types:</h6>
			<div class="form-check">
				<input
					class="form-check-input"
					type="checkbox"
					bind:checked={settings.sentenceTypes.positive}
					id={settings.sentenceTypes.positive}
				/>
				<label class="form-check-label" for={settings.sentenceTypes.positive}
					>Positive</label
				>
			</div>
			<div class="form-check">
				<input
					class="form-check-input"
					type="checkbox"
					bind:checked={settings.sentenceTypes.negative}
					id={settings.sentenceTypes.negative}
				/>
				<label class="form-check-label" for={settings.sentenceTypes.negative}
					>Negative</label
				>
			</div>
			<div class="form-check">
				<input
					class="form-check-input"
					type="checkbox"
					bind:checked={settings.sentenceTypes.question}
					id={settings.sentenceTypes.question}
				/>
				<label class="form-check-label" for={settings.sentenceTypes.question}
					>Question</label
				>
			</div>
		</div>
	</div>

	<div class="row">
		<div class="col">...</div>
	</div>

	<div class="row">
		<div class="col">...</div>
	</div>

	<div class="row">
		<div class="col">...</div>
	</div>

	<div class="row">
		<div class="col">...</div>
	</div>

	<hr />

	<!-- Docs -->
	<div class="row">
		<div class="col">
			<h3 class="text-center">Docs</h3>
		</div>
	</div>

	{#each tenses as tense}
		{#if tense.enabled}
			<div class="row">
				<div class="col">
					<h6 style="text-align: center">{tense.title}</h6>
					<table class="table table-striped table-hover table-sm">
						<thead>
							<tr>
								<th>Positive</th>
								<th>Negative</th>
								<th>Question</th>
							</tr>
						</thead>
						<tbody>
							{#each [0, 1, 2, 3, 4, 5, 6] as idx}
								<tr>
									<td>{tense.sentences.positive[idx]}</td>
									<td>{tense.sentences.negative[idx]}</td>
									<td>{tense.sentences.question[idx]}</td>
								</tr>
							{/each}
						</tbody>
					</table>
				</div>
			</div>
		{/if}
	{/each}
</div>

<style>
	button {
		min-height: 80px;
	}

	.btn-outline-primary:hover {
		background-color: #fff;
		border-color: #007bff;
		color: #007bff;
	}
</style>
